@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
.navigationBar {
  width: 265px;
  border: 0.2px solid #f1e8e8;
  height: 71px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
  color: rgba(241, 232, 232, 0.6);
  text-decoration: none;
}

.navigationBar:hover {
  -webkit-animation: rotateLink 0.5s forwards ease-out;
          animation: rotateLink 0.5s forwards ease-out;
  background-color: #4a4646;
}

@-webkit-keyframes rotateLink {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(180deg);
  }
}

@keyframes rotateLink {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(180deg);
  }
}

.home {
  background: #3d3c3c;
  height: 100vh;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
}

.home__container {
  background: #0a0909;
  border-radius: 5px;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  overflow: hidden;
}

.home__navigation1 {
  position: absolute;
  top: 50%;
  left: 0;
  margin-left: 12px;
  transform: translateY(-50%);
  z-index: 10;
}

.home__navigation2 {
  position: absolute;
  margin-right: 12px;
  top: 50%;
  z-index: 10;
  right: 0;
  transform: translateY(-50%);
}

.home__brand {
  margin-top: 4rem;
  font-style: normal;
  font-weight: normal;
  font-size: 62px;
  line-height: 73px;
  text-align: center;
}

.home__images {
  max-height: 600px;
  display: flex;
  max-width: 800px;
  align-items: center;
  justify-content: center;
  margin: auto;
  position: relative;
}

.home__ctx {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin-bottom: 10px;
}
.home__ctx > h5,
.home__ctx a h5 {
  font-weight: 300;
  cursor: pointer;
}

.home__ctx > a h5:hover {
  color: rgb(245, 0, 87);
}

.home__spotlight {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home__spotlight > p {
  text-align: center;
  font-size: 16px;
  margin-top: -93px;
  color: #6f7377;
  position: relative;
  letter-spacing: normal;
  line-height: 32px;
}

.home__spotlight > h1 {
  display: block;
  color: #fff;
  font-size: 4rem;
  line-height: 1;
  font-family: neuemachina-ultrabold;
  letter-spacing: normal;
  text-transform: capitalize;
  margin-top: 2rem;
}

@media (max-width: 700px) {
  .home__brand {
    font-size: 6vw;
  }
  .home__navigation1 {
    transform: rotate(90deg) !important;
    left: -16%;
  }
  .home__navigation2 {
    transform: rotate(90deg) !important;
    right: -16%;
  }
}

.header {
  padding: 2rem 3rem;
  width: 100%;
  position: fixed;
  z-index: 100000;
  -webkit-animation: switchLeft 0.5s forwards 0.8s
    cubic-bezier(0.175, 0.885, 0.32, 1.275);
          animation: switchLeft 0.5s forwards 0.8s
    cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.header > a,
.header--workLink,
.header--brand {
  text-decoration: none;
  color: white !important;
  position: relative;
}

.header--workLink {
  padding-left: 4rem;
}

.header--workLink::after {
  content: '';
  width: 46px;
  height: 1px;
  border: none;
  border-top: 1px solid #fff;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translateY(-50%);
  display: block;
  box-sizing: content-box;
}

@-webkit-keyframes switchLeft {
  from {
    transform: translateX(0vw);
  }
  to {
    transform: translateX(70vw);
  }
}

@keyframes switchLeft {
  from {
    transform: translateX(0vw);
  }
  to {
    transform: translateX(70vw);
  }
}

@media (max-width: 500px) {
  .header {
    margin: auto;
    padding: 1rem 10%;
    -webkit-animation: none;
            animation: none;
  }
  .header:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.about {
  background: #0a0909;
  color: #f1e8e8;
  min-height: 100vh;
  padding: 2rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: neuemachina-ultrabold;
  width: 100%;
  overflow-x: hidden;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  width: 100vw;
  height: 100vh;
}

.about__content {
  width: 528px;
  float: left;
  margin-left: 18.75%;
  margin-bottom: 140px;
}

.about__tileMusk {
  font-family: neuemachina-ultrabold;
  font-size: 54px;
  line-height: 80px;
  color: #ffffff;
  display: block;
  clear: both;
  border-bottom: 1px solid #fff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.about__me {
  position: relative;
  margin-top: 219px;
  width: 37.56%;
  max-width: 397px;
  float: left;
  margin-left: 42px;
}

.about__techs {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 0 5rem;
}

.about__techImg {
  width: 50px;
  object-fit: contain;
}

.about__techItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  flex: 1 1;
}

.about__techItem > h4 {
  opacity: 0;
  pointer-events: none;
  margin-top: 10px;
  border: 1px solid lightblue;
  padding: 5px 10px;
  font-weight: 400;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.about__techItem:hover > h4 {
  opacity: 1;
  -webkit-animation: scaleUp 0.1s ease-out;
          animation: scaleUp 0.1s ease-out;
}

@-webkit-keyframes scaleUp {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

@keyframes scaleUp {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

@media (max-width: 855px) {
  .about__techs {
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .about__techImg {
    margin: 0.5rem;
    flex: 0.1 1;
  }
}

@media (max-width: 700px) {
  .about__techItem {
    margin: 10px;
  }
}

@media (max-width: 650px) {
  .about__content {
    width: 100%;
    margin-left: 12px;
    margin-bottom: -10px;
    margin-top: 50px;
  }
  .about__me {
    margin: 55px 50px;
  }
}

@media (max-width: 350px) {
  .about__techItem {
    margin: auto;
  }
  .about__techs {
    padding: 0 2px;
  }

  .about__tileMusk {
    font-size: 2rem;
  }
}

.project {
  display: flex;
  height: 100vh;
  padding: 5rem 0;
  position: relative;
  overflow: hidden;
  margin: 1rem 0;
}

.project__media {
  position: relative;
  flex: 0.7 1;
}

.project__media > img {
  width: 100%;
  height: 100%;
  /* max-width: 1000px; */
  justify-content: center;
  align-items: center;
  background-position: 100% 0%;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: scroll;
}

.project__name--backdrop {
  position: absolute;
  left: 0%;
  top: -20%;
  right: auto;
  bottom: auto;
  z-index: 1;
  display: block;
  width: 95vw;
  perspective: 1100px;
  perspective-origin: 50% 100%;
  opacity: 0.8;
}

.project__name {
  font-size: 8vw;
  line-height: 1.2em;
  font-weight: 900;
  -webkit-text-stroke: 2px #fff;
  text-transform: uppercase;
  color: transparent;
}

.project__metaData {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex: 0.1 1;
}

.project__details {
  transform: rotate(90deg);
  opacity: 0.6;
  color: #fff;
  font-size: 10px;
  line-height: 1em;
  font-weight: 300;
  letter-spacing: 1px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  text-transform: uppercase;
}
.project__link {
  position: relative;
  z-index: 5;
  display: flex;
  width: 150px;
  height: 150px;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  border-radius: 50%;
  background-color: transparent;
  border: 2px solid #12e09b;
  opacity: 0.9;
  cursor: pointer;
}

.project__link > a {
  text-decoration: none;
  margin-left: auto;
  margin-right: auto;
  color: #fff;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 2500ms;
          animation-duration: 2500ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.project__idx {
  flex: 0.2 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 10vw;
  line-height: 1em;
  font-weight: 900;
  -webkit-text-stroke: 2px #fff;
  text-transform: uppercase;
  color: transparent;
  opacity: 0.1;
}

.project__idx > h1 {
  transform: rotate(90deg);
}

@media screen and (max-width: 700px) {
  .project {
    flex-direction: column;
  }

  .project__idx > h1,
  .project__details {
    transform: rotate(0deg);
  }

  .project__name {
    padding-left: 1rem;
  }

  .project__media {
    flex: 0.6 1;
  }
  .project__idx {
    height: 100%;
    flex-direction: row;
    justify-content: flex-end;
    align-items: baseline;
  }

  .project__metaData {
    flex-direction: row;
    padding: 0 5rem;
  }
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.works {
  height: 100%;
  min-height: 100vh;
  background: #0a0909;
  overflow-x: hidden;
  position: relative;
}

.works__lists {
  padding-top: 5rem;
}

.app {
  transition: background-color 1.5s cubic-bezier(0.65, 0.05, 0.36, 1);
  min-height: 100vh;
}

@font-face {
  font-family: neuemachina-ultrabold;
  src: url(/static/media/neuemachina-ultrabold.afcd8980.woff2);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: #ebebeb;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #6d6d6d;
}

body {
  margin: 0;
  font-family: neuemachina-ultrabold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  /* background-color: #070219; */
  position: relative;
  min-height: 100vh;
  width: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #fff;
}

